<template>
	<div>
		<div class="goods">
			<div class="menu-wrapper" ref="menuWrapper">
				<ul>
					<li v-for="(item,index) in goods" :key="index" class="menu-item" ref="menuList" @click="selectMenu(index, $event)" :class="{'current':currentIndex == index}">
						<span class="text border-1px">
							<span v-show="item.type > 0" class="icon" :class="classMap[item.type]">!!</span>{{item.name}}
						</span>
					</li>
				</ul>
			</div>
			<div class="foods-wrapper" ref="foodsWrapper">
				<ul>
					<li v-for="(item,index) in goods" class="food-list" ref="foodList" :key="index" >
						<h1 class="title">{{item.name}}</h1>
						<ul>
							<li v-for="(food,index) in item.foods" :key="index" class="food-item border-1px" @click="selectFood(food, $event)">
								<div class="icon">
									<img width="57px" :src="food.icon">
								</div>
								<div class="content">
									<h2 class="name">{{food.name}}</h2>
									<p class="desc">{{food.description}}</p>
									<div class="extra">
										<span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
									</div>
									<div class="price">
										<span class="now">${{food.price}}</span>
										<span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
									</div>
									<div class="cartcontrol-wrapper">
										<cartcontrol :food="food" @add="addFood"></cartcontrol>
									</div>
								</div>
							</li>
						</ul>
					</li>
				</ul>
			</div>
			<shopcart ref="shopcart" :selected-foods="selectedFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice">
			</shopcart>
		</div>
		<food :food="selectedFood" ref="food"></food>
	</div>
</template>

<script>
	import BScroll from 'better-scroll';
	import shopcart from 'components/shopcart/shopcart';
	import cartcontrol from 'components/cartcontrol/cartcontrol';
	import food from 'components/food/food';

	const ERR_OK = 0;
	export default {
		props: {
			seller: {
				type: Object
			}
		},
		data () {
			return {
				goods: [],
				listHeight: [],
				scrollY: 0,
				selectedFood: {}
			};
		},
		created () {
			this.$http.get('/api/goods').then((response) => {
				response = response.body;
				if (response.errno === ERR_OK) {
					this.goods = response.data;
					console.log('goods:' + this.goods);
					this.$nextTick(() => {
						this._initScroll();
						this._calculateHeight();
					});
				}
			});
			this.classMap = ['decrease', 'discount', 'special', 'invoice', 'gurantee'];
		},
		computed: {
			currentIndex () {
				for (let i = 0; i < this.listHeight.length - 1; i++) {
					let height1 = this.listHeight[i];
					let height2 = this.listHeight[i + 1];
					if (this.scrollY >= height1 && this.scrollY < height2) {
						return i;
					}
				}
				return 0;
			},
			selectedFoods () {
				let foods = [];
				this.goods.forEach((good) => {
					good.foods.forEach((food) => {
						if (food.count) {
							foods.push(food);
						}
					});
				});
				return foods;
			}
		},
		methods: {
			_initScroll () {
				this.menuScroll = new BScroll(this.$refs.menuWrapper, {
					click: true
				});
				this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
					click: true,
					probeType: 3
				});
				this.foodsScroll.on('scroll', (pos) => {
					this.scrollY = Math.abs(Math.round(pos.y));
				});
			},
			_calculateHeight () {
				let foodList = this.$refs.foodList;
				let height = 0;
				this.listHeight.push(height);
				for (let i = 0; i < foodList.length; i++) {
					let item = foodList[i];
					height += item.clientHeight;
					this.listHeight.push(height);
				}
			},
			selectMenu (index, event) {
				if (!event._constructed) {
					return;
				}
				console.log(index);
				let foodList = this.$refs.foodList;
				let el = foodList[index];
				this.foodsScroll.scrollToElement(el, 300);
			},
			_drop (target) {
				// 使用nextTick让动画异步执行，这样不会卡
				this.$nextTick(() => {
					this.$refs.shopcart.drop(target);
				});
			},
			addFood (target) {
				this._drop(target);
			},
			selectFood (food, event) {
				if (!event._constructed) {
					return;
				}
				this.selectedFood = food;
				this.$refs.food.show();
			}
		},
		components: {
			shopcart,
			cartcontrol,
			food
		}
	};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin"
.goods
	position absolute
	top 174px
	bottom 46px
	display flex
	width 100%
	overflow hidden
	.menu-wrapper
		flex 0 0 80px
		width 80px
		background #f3f5f7
		.menu-item
			display table
			height 54px
			width 56px
			line-height 14px
			padding 0 12px
			&.current
				position relative
				z-index 10
				margin-top -1px
				background #fff
				font-weight 700
				.text
					border-none()
			.icon
				display inline-block
				vertical-align top
				width 12px
				height 12px
				margin-right 2px
				background-size 12px
				background-repeat no-repeat
				&.decrease
					bg-image('decrease_3')
				&.discount
					bg-image('discount_3')
				&.gurantee
					bg-image('guarantee_3')
				&.invoice
					bg-image('invoice_3')
				&.special
					bg-image('special_3')
			.text
				display table-cell
				vertical-align middle
				font-size 12px
				border-1px(rgba(7,17,27,0.1))
	.foods-wrapper
		flex 1
		.title
			padding-left 14px
			height 26px
			line-height 26px
			border-left 2px solid #d9dde1
			font-size 12px
			color rbg(147,153,59)
			background #f3f5f7
		.food-item
			display flex
			margin 18px
			padding-bottom 18px
			border-1px(rgba(7,17,27,0.1))
			&:last-child
				border-none()
				margin-bottom 0
			.icon
				flex 0 0 57px
				margin-right 10px
			.content
				flex 1
				.name
					margin 2px 0 8px 0
					height 14px
					line-height 14px
					font-size 14px
					color rgb(7,17,27)
				.desc, .extra
					line-height 10px
					font-size 10px
					color rgb(147, 153, 159)
					margin-bottom 8px
				.desc
					margin-bottom 8px
					line-height 12px
				.extra
					.count
						margin-right 12px
				.price
					font-weight 700
					line-height 14px
					font-size 0px
					.now
						margin-right 8px
						font-size 14px
						color rgb(240,20,20)
					.old
						text-decoration line-through
						font-size 10px
						color rgb(147,153,159)
				.cartcontrol-wrapper
					position absolute
					right 0px
					bottom 12px
</style>



