<template>
<div class="header">
	<div class="content-wrapper">
		<div class="avatar">
			<img width="64" :src="seller.avatar">
		</div>
		<div class="content">
			<div class="title">
				<span class="brand"></span>
				<span class="name">{{seller.name}}</span>
			</div>
			<div class="description">
				{{seller.description}}/{{seller.deliveryTime}}分钟送达
			</div>
			<div v-if="seller.supports" class="support" @click="showDetail()">
				<span class="icon" :class="classMap[seller.supports[0].type]">{{seller.supports[0].type}}</span>
				<span class="text">{{seller.supports[0].description}}</span>
			</div>
		</div>
	</div>
	<div class="bulletin-wrapper" @click="showDetail()">
		<span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
		<i class="icon-keyboard_arrow_right"></i>
	</div>
	<div class="background">
		<img :src="seller.avatar" width="100%" height="100%">
	</div>
	<transition name="fade">
		<div class="detail" v-show="detailShow" transition="fade">
			<div class="detail-wrapper clearfix">
				<div class="detail-main">
					<h1 class="name">{{seller.name}}</h1>
					<div class="star-wrapper">
						<star size='big' :score='seller.score'></star>
					</div>
					<div class="title">
						<div class="line"></div>
						<div class="text">优惠信息</div>
						<div class="line"></div>
					</div>
					<ul v-if="seller.supports" class="supports">
						<li v-for="(item,index) in seller.supports" :key='index' class="support-item">
							<span class="icon" :class="classMap[seller.supports[index].type]"></span>
							<span class="text">{{seller.supports[index].description}}</span>
						</li>
					</ul>
					<div class="title">
						<div class="line"></div>
						<div class="text">商家公告</div>
						<div class="line"></div>
					</div>
					<div class="bulletin">
						<p class="content">{{seller.bulletin}}</p>
					</div>
				</div>
			</div>
			<div class="detail-close" @click="closeDetail()">
				<i class="icon-close"></i>
			</div>
		</div>
	</transition>
</div>
</template>

<script type="text/ecmascript-6">
	import star from 'components/star/star';
	export default {
		props: {
			seller: {
				type: Object
			}
		},
		data () {
			return {
				detailShow: false
			};
		},
		methods: {
			showDetail () {
				this.detailShow = true;
			},
			closeDetail () {
				this.detailShow = false;
			}
		},
		created () {
			this.classMap = ['decrease', 'discount', 'special', 'invoice', 'gurantee'];
		},
		components: {
			star
		}
	};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin.styl";

.header
	position relative
	color: #fff
	background-color rgba(7,17,27,0.5)
	overflow hidden
	.content-wrapper
		font-size: 0
		padding: 24px 12px 18px 24px
		.avatar
			display: inline-block
			vertical-align top
		.content
			margin-left: 16px
			display: inline-block
			font-size: 14px
			.title
				margin: 2px 0 8px 0
				.brand
					display: inline-block
					width: 30px
					height: 18px
					bg-image('brand')
					background-size: 30px 18px
					background-repeat: no-repeat
					vertical-align: top
				.name
					margin-left: 6px
					font-size: 16px
					line-height: 18px
					font-weight: bold
			.description
				font-size: 12px
				line-height: 12px
				margin-bottom: 10px
			.support
				.icon
					display inline-block
					width 12px
					height 12px
					margin-right 4px
					background-size 12px
					background-repeat no-repeat
					&.decrease
						bg-image('decrease_1')
					&.discount
						bg-image('discount_1')
					&.gurantee
						bg-image('guarantee_1')
					&.invoice
						bg-image('invoice_1')
					&.special
						bg-image('special_1')
				.text
					vertical-align top
					line-height 12px
					font-size 12px
	.bulletin-wrapper
		height 28px
		line-height 28px
		padding 0 22px 0 12px
		white-space nowrap
		overflow hidden
		text-overflow ellipsis
		font-size 10px
		position relative
		background rgba(7,17,27,0.2)
		.bulletin-title
			display inline-block
			vertical-align middle
			width 22px
			height 12px
			bg-image('bulletin')
			background-size 22px 12px
			background-repeat no-repeat
		.bulletin-text
			vertical-align middle
			line-height 12px
			font-weight 200
			font-size 10px
			margin 0 4px
		.icon-keyboard_arrow_right
			position absolute
			font-size 14px
			right 10px
			top 8px
			line-height 14px
			vertical-align middle
	.background
		position absolute
		top 0px
		left 0px
		width 100%
		height 100%
		z-index -1
		filter blur(10px)
	.detail
		position fixed
		top 0px
		left 0px
		z-index 100
		width 100%
		height 100%
		overflow auto
		background-color rgba(7,17,27,0.8)
		&.fade-enter, &.fade-leave-active
			opacity 0
		&.fade-enter-active, &.fade-leave-active
			transition all 0.5s
		.detail-wrapper
			min-height 100%
			width 100%
			.detail-main
				margin-top 64px
				padding-bottom 64px
				.name
					line-height 16px
					text-align center
					font-size 16px
					font-weight 700
				.star-wrapper
					margin-top 18px
					padding 2px 0
					text-align center
				.title
					display flex
					width 80%
					margin 30px auto 24px auto
					.line
						flex 1
						position relative
						top -6px
						border-bottom 1px solid rgba(255,255,255,0.2)
					.text
						padding 0 12px
						font-size 14px
						font-weight 700
				.supports
					width 80%
					margin 0 auto
					.support-item
						padding 0 12px
						font-size 0
						margin-bottom 12px
						&.last-child
							margin-bottom 0
						.icon
							display inline-block
							width 16px
							height 16px
							vertical-align top
							margin-right 6px
							background-size 16px
							background-repeat no-repeat
							&.decrease
								bg-image('decrease_2')
							&.discount
								bg-image('discount_2')
							&.gurantee
								bg-image('guarantee_2')
							&.invoice
								bg-image('invoice_2')
							&.special
								bg-image('special_2')
						.text
							font-size 13px
							line-height 16px
				.bulletin
					width 80%
					margin 0 auto
					.content
						font-size 13px
						padding 0 12px
						line-height 24px
						font-weight 500
		.detail-close
			position relative
			width 32px
			height 32px
			margin -64px auto 0 auto
			clear both
			font-size 32px
</style>



